.search-list {
  border: solid #7c96ab 0.5px;
  position: absolute;
  left: 12px;
  top: 32px;
  z-index: 1;
  width: 302px;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
  max-height: 140px;
  overflow-y: scroll;
  text-align: left;

  background-color: #fff;
  cursor: pointer;
}

.single-item {
  padding: 5px;
  opacity: 0.3;
}
.single-item:hover {
  background-color: #b7b7b7;
  opacity: 0.85;
  font-weight: 600;
}
