.nav-comp {
  position: absolute;
  height: 100vh;
  width: 100vw;
}

nav {
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  right: 0;
  margin: auto;
}

nav ul {
  list-style-type: none;
}

nav ul li {
  margin: 20px 0;
  font-size: 1.2rem;
}

nav a {
  text-decoration: none;
}

nav ul li a {
  color: #b7b7b7;
  padding-left: 10px;
}

nav ul li a:hover {
  color: #7c96ab;
}

nav ul li a.active {
  color: #7c96ab;
  border-left: solid 2px #7c96ab;
  font-weight: 600;
}
