.read-file {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  width: 1000px;
  height: 450px;
  left: 0;
  right: 0;
  margin: 100px auto;
  background-color: #fff;
  padding-top: 25px;
  border-radius: 1rem;
  box-shadow: rgba(9, 30, 66, 0.25) 0px 4px 8px -2px,
    rgba(9, 30, 66, 0.08) 0px 0px 0px 1px;
}

.read-file label {
  margin: 10px;
}

.read-file input {
  margin: 10px;
  width: 300px;
}

.dropdown-items select {
  width: 300px;
  margin: 10px;
}
.dropdown-items {
  margin: 0 100px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.read-file .btn {
  cursor: pointer;
  width: 150px;
  margin: 30px auto;
  font-size: 1rem;
  padding: 10px 20px;
  border-radius: 1px;
  outline: none;
  border: none;
  color: #fff;
  font-family: Georgia;
  font-weight: 400;
  background-color: #7c96ab;
}
.open-file.btn {
  margin-bottom: 60px;
  width: 100%;
  font-size: 1.5rem;
  background: transparent;
  color: #7c96ab;
  cursor: default;
}
/* .open-file.btn:hover {
  background-color: #7c96ab;
  color: #fff;
} */
.filter-submit.btn {
  opacity: 1;
  margin: 50px auto 50px;
}
.filter-submit.btn:hover {
  opacity: 0.8;
}

.read-file select:hover {
  cursor: pointer;
  border: solid 2px #fff;
  box-shadow: #7c96ab 0px 4px 8px -2px, #7c96ab 0px 0px 0px 1px;
}

.dropdown-items input:hover {
  cursor: pointer;
  border: solid 2px #fff;
  box-shadow: #7c96ab 0px 4px 8px -2px, #7c96ab 0px 0px 0px 1px;
}
.dropdown-items input:focus {
  cursor: auto;
  outline: none !important;
}

.dropdown-flex {
  position: relative;
}

.open-file > svg {
  font-size: 1.5rem;
  margin-right: 5px;
  animation-name: loading-data;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-fill-mode: forwards;
}

@keyframes loading-data {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.btn.refresh {
  position: absolute;
  top: 0;
  right: 35px;
  width: 80px;
  font-size: 0.8rem;
  background: transparent;
  border: solid #7c96ab 1px;
  color: #7c96ab;
  opacity: 0.7;
}

.btn.refresh:hover {
  background-color: #7c96ab;
  color: #fff;
  opacity: 1;
}
