.report-page {
  text-align: center;
}

.report-page .background-img {
  z-index: -1;
  position: fixed;
  height: 100vh;
  width: calc(100% - 300px);
  right: 0;
  background-color: #7c96ab;
}

.report-page h1 {
  color: #fff;
  margin: 100px 0;
  font-size: 2.5rem;
}

.report-page h3 {
  font-size: 1.5rem;
  color: #fff;
  margin: 30px auto 50px;
}

.report-page .author-cards {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  padding-bottom: 100px;
}

.report-page .author-cards img {
  height: 300px;
  width: auto;
  border-radius: 1rem;
}

.report-page .author-cards p {
  width: 450px;
  font-size: 1.3rem;
  line-height: 1.2;
}

.report-page .data-intro {
  padding-bottom: 150px;
  width: 80%;
  margin: auto;
  font-size: 1.3rem;
  line-height: 1.5;
}

.institutions .logos {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-bottom: 150px;
}

.institutions .logos img {
  margin: 50px 50px 0;
  height: 50px;
  width: auto;
}

.report-page a {
  text-decoration: none;
  color: #7c96ab;
  font-weight: 600;
  font-size: 1rem;
}

.report-page button {
  margin: 50px 25px 0;
  padding: 10px 20px;
  outline: none;
  border: solid 2px #fff;
  background-color: #fff;
  opacity: 1;
  width: 200px;
}

.report-page button:hover {
  opacity: 0.8;
}
