.logo-comp {
  position: fixed;
  left: -50px;
  top: 50px;
  transform: translateX(50%);
  text-align: center;
  background-color: #7c96ab;
  color: #fff;
  font-weight: 200;
  padding: 0 27px;
  cursor: pointer;
}

.logo-comp p {
  margin: 10px 0;
}

.logo-comp p:nth-child(2) {
  font-size: 2rem;
  font-weight: 600;
}
