.contact-page {
  text-align: center;
}

.contact-page .background-img {
  z-index: -1;
  position: fixed;
  height: 100vh;
  width: calc(100% - 300px);
  right: 0;
  background-color: #7c96ab;
}

.contact-page h1 {
  margin: 7% 0 80px;
  color: #fff;
}

.contact-page p {
  color: #fff;
}

.contact-page form div {
  margin: 30px;
}

::placeholder {
  color: #fff;
  opacity: 1; /* Firefox */
  font-family: 'Courier New', Courier, monospace;
  font-weight: 600;
}

.contact-page form input {
  padding: 10px;
  width: 400px;
  outline: none !important;
  border: solid 2px #fff;
  background: transparent;
  color: #fff;
  font-size: 1rem;
}
.contact-page form input:hover {
  border: solid 2px #b7b7b7;
}
.contact-page form input:focus {
  border: solid 2px #b7b7b7;
}

.contact-page form textarea {
  padding: 10px;
  width: 400px;
  height: 120px;
  outline: none !important;
  border: solid 2px #fff;
  background: transparent;
  color: #fff;
  font-size: 1.2rem;
}
.contact-page form textarea:hover {
  border: solid 2px #b7b7b7;
}
.contact-page form textarea:focus {
  border: solid 2px #b7b7b7;
}

.contact-page form .btn {
  display: block;
  left: 0;
  right: 0;
  margin: 50px auto 100px;
  border: none;
  width: 250px;
}

.contact-page form .btn {
  -webkit-border-radius: 1;
  -moz-border-radius: 1;
  border-radius: 1px;
  font-family: Georgia;
  color: #7c96ab;
  font-size: 1rem;
  font-weight: 600;
  padding: 10px 20px;
  text-decoration: none;
  background: #fff;
  outline: solid #fff;
}

.contact-page form .btn:hover {
  color: #fff;
  background: #b7b7b7;
  background-image: -webkit-linear-gradient(top, #b7b7b7, #b7b7b7);
  background-image: -moz-linear-gradient(top, #b7b7b7, #b7b7b7);
  background-image: -ms-linear-gradient(top, #b7b7b7, #b7b7b7);
  background-image: -o-linear-gradient(top, #b7b7b7, #b7b7b7);
  background-image: linear-gradient(to bottom, #b7b7b7, #b7b7b7);
  text-decoration: none;
  outline: solid #7c96ab;
  cursor: pointer;
}

.contact-page form .btn:disabled {
  background: #fff;
  outline: solid #fff;
}
