.home-page {
  text-align: right;
}

.home-page .background-img {
  z-index: -1;
  position: fixed;
  height: 100vh;
  width: calc(100% - 300px);
  right: 0;
  background-color: #7c96ab;
}
.home-page .aasb-logo {
  position: absolute;
  bottom: 0px;
  width: 100%;
  background-color: #313030;
  text-align: left;
}
.home-page .aasb-logo img {
  height: 90px;
}

.home-page .title {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-80%);
  width: 90%;
}

.home-page .title p {
  color: #fff;
  font-size: calc(100vh * 0.05);
  line-height: 1.3;
  margin: 0;
  padding-right: 50px;
  font-weight: 800;
}
.home-page .title .sub-title {
  font-size: calc(100vh * 0.03);
  padding-top: 60px;
  margin: 0;
  font-weight: 400;
}
