.slides-comp {
  position: relative;
  width: 1000px;
  height: 700px;
  left: 0;
  right: 0;
  margin: 50px auto 150px;
  background-color: #fff;
  border-radius: 1rem;
  box-shadow: rgba(9, 30, 66, 0.25) 0px 4px 8px -2px,
    rgba(9, 30, 66, 0.08) 0px 0px 0px 1px;
}

.slides-comp .change {
  font-size: 2.5rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  color: #7c96ab;
  opacity: 0.6;
}
.slides-comp .change:hover {
  opacity: 1;
}
.slides-comp .change.left {
  left: 50px;
}
.slides-comp .change.right {
  right: 50px;
}

.slides-comp .show-cards {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 50%;
  transform: translateY(-52%);
  width: 80%;
  height: 75%;
}

.slides-comp .show-cards .card {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 50%;
  transform: translateY(-45%);
}

.slides-comp .show-cards .card form div {
  padding: 0 70px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 40px 0;
}
.slides-comp .show-cards .card form div:last-child {
  font-weight: 600;
  color: #d14d72;
}

.slides-comp .show-cards .card form div label {
  font-size: 1.5rem;
}

.slides-comp .show-cards .card form div input {
  padding: 5px 10px;
  font-size: 1.1rem;
  width: 350px;
  background: transparent;
  border: solid 0.5px;
  border-radius: 0.2rem;
  outline: none !important;
}
.slides-comp .show-cards .card form div input:hover {
  cursor: pointer;
  border: solid 0.5px #fff;
  box-shadow: #7c96ab 0px 4px 8px -2px, #7c96ab 0px 0px 0px 1px;
}

.slides-comp .footer {
  font-size: 1.5rem;
  position: absolute;
  bottom: 40px;
  left: 0;
  right: 0;
  margin: auto;
  color: #7c96ab;
}

.slides-comp .heading {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 50px;
  color: #7c96ab;
  font-size: 2rem;
}
